import {
  request
} from "./request"
import qs from "qs"

//   获取代理池列表
export function getProxyList(queryVo) {
  return request({
    url: "/getProxyList",
    params: {
      currentPage: queryVo.currentPage,
      pageSize: queryVo.pageSize,
      reverse: queryVo.reverse
    }
  })
}

// 获取代理池总数
export function getProxyCount() {
  return request({
    url: "/getProxyCount"
  })
}

// 删除指定代理
export function removeProxy(proxy) {
  return request({
    url: "/removeProxy",
    method: 'POST',
    contentType: "application/x-www-form-urlencoded",
    data: qs.stringify({
      proxy
    })
  })
}