<template>
  <a-page-header class="header" :title="title">
    <template slot="tags">
      <a-tag color="blue">{{subtitle}}</a-tag>
    </template>
    <template slot="extra">
      <a-dropdown class="dropdown">
        <a>
          {{username}}
          <a-icon type="down" />
        </a>
        <a-menu slot="overlay">
          <a-menu-item>
            <a @click="logout">注销</a>
          </a-menu-item>
        </a-menu>
      </a-dropdown>
      <a-statistic title="代理可用总数" :value="proxyCount" class="proxy" style="text-align: center;" />
    </template>
  </a-page-header>
</template>

<script>
import { SET_USER } from "store/mutation_types";
import { getProxyCount } from "network/proxy";

export default {
  name: "PageHeader",
  data() {
    return {
      proxyCount: 0
    };
  },
  computed: {
    username() {
      return this.$store.getters.user.username;
    },
    title() {
      return this.$route.meta.title;
    },
    subtitle() {
      return this.$route.meta.subtitle;
    }
  },
  mounted() {
    this.getCount();
    this.$bus.$on("removeProxy", this.refresh);
  },
  // beforeDestroy() {
  //   this.$bus.$off("removeProxy", this.refresh);
  // },
  methods: {
    // 登出
    logout() {
      this.$router.push("/login");
      window.localStorage.removeItem("token");
      this.$store.commit(SET_USER, {
        user: {
          id: "",
          username: ""
        }
      });
    },
    // 获取代理池总数
    getCount() {
      let count = () => {
        getProxyCount().then(res => {
          this.proxyCount = res.data.data.total;
        });
        return count;
      };
      setInterval(count(), 10000);
    },
    refresh() {
      getProxyCount().then(res => {
        this.proxyCount = res.data.data.total;
      });
    }
  }
};
</script>

<style scoped>
tr:last-child td {
  padding-bottom: 0;
}
.proxy {
  float: right;
  margin-left: 30px;
}
.header {
  padding-left: 0;
  padding-right: 0;
}
.dropdown {
  font-size: 16px;
  margin-right: 25px;
}
</style>